@import '../../../../helpStyles/variables.scss';

.auth__form {
  display: flex;
  flex-direction: column;
  align-items: center;
  row-gap: 20px;
  margin: 20px 0;
}

.auth__input {
  width: 310px;
  padding: 10px 15px;
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  letter-spacing: 0.03em;
  color: #0009;
}

.auth__btn {
  padding: 10px 25px;
  border-radius: 8px;
  background-color: $main-color;
  font-size: 16px;
  line-height: 22px;
  letter-spacing: 0.01em;
  color: #fafafa;
  transition: all 0.2s ease;
  &:active {
    transform: scale(0.97);
  }
  &:disabled:active {
    transform: scale(1);
  }
}

.auth__acc {
  display: flex;
  justify-content: center;
  align-items: center;
  column-gap: 5px;
  font-weight: 400;
  font-size: 12px;
  line-height: 16px;
  letter-spacing: 0.01em;
}

.auth__acc-btn {
  background-color: transparent;
  font-size: 14px;
  font-weight: 600;
  color: $main-color;
}

.inputPass__container {
  position: relative;
}

.inputPass__showBtn {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 5%;
  background-color: transparent;
}

@media (min-width: 1024px) {
  .auth__acc-btn {
    transition: all 0.1s ease;
    &:hover {
      text-decoration: underline;
    }
    &:active {
      transform: scale(0.97);
    }
  }
}

@media (max-width: 414px) {
  .auth__input {
    width: 260px;
  }
}
